.add-btn {
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  padding: 3px 10px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
  border: 1px solid black;
  transition: 0.2s;
}

.add-btn:hover {
  background-color: #616161;
  color: white;
}

.refresh {
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  padding: 3px 10px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  border: 1px solid black;
  transition: 0.2s;
}

.refresh:hover {
  background-color: #0d6efd;
  color: white;
}

.add-btn i {
  font-weight: bolder !important;
  font-size: larger;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.modal-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.modal-content h3 {
  margin-bottom: 20px;
  text-align: center;
}

.form-style input,
.form-style select {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 15px;
}

.form-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.submit-btn,
.cancel-btn {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  cursor: pointer;
}

.submit-btn {
  background-color: #28a745;
  color: white;
}

.cancel-btn {
  background-color: #dc3545;
  color: white;
}


.success {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  padding: 12px;
  border-radius: 4px;
  color: #155724;
  margin-bottom: 1rem;
  animation: fadeIn 0.5s;
}

.error {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  padding: 12px;
  border-radius: 4px;
  color: #721c24;
  margin-bottom: 1rem;
  animation: fadeIn 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}


input:focus,
select:focus {
  box-shadow: none !important;
  border: 1px solid rgb(134, 134, 134) !important;
}

@media (max-width: 766px) {
  .container {
    min-width: 100%;
  }
}

.del {
  display: flex;
  justify-content: center;
  align-items: center;
}


.del button {
  border: none;
  background-color: rgba(255, 255, 255, 0);
  transition: 0.2s;
}

.del button:hover {
  color: red;
}

.confirm button {
  padding: 4px 8px;
}

.error {
  background-color: #f8d7da;
  color: #721c24;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
  font-weight: 600;
}

.rec {
  background-color: white;
  border: 1px solid black;
  border-radius: 0.3rem;
  transition: 0.2s;
}

.rec:hover {
  background-color: #414141;
  color: white;
}

.dels {
  background-color: white;
  color: black;
  border: 1px solid black;
  border-radius: 0.3rem;
  transition: 0.2s;
}

.dels:hover {
  background-color: #ff273c;
  color: white;
}

textarea {
  height: 30vh !important;
  resize: none;
}

textarea:focus {
  box-shadow: none !important;
  border-color: #7e7e7e !important;
}

.alert-themed-blue {
  background-color: #cce5ff;
  color: #004085;
  border: 1px solid #b8daff;
}

.alert-neutral {
  background-color: #f8f9fa;
  color: #6c757d;
  border: 1px solid #dee2e6;
}

.alert-highlight {
  background-color: #fff3cd;
  color: #856404;
  border: 1px solid #ffeeba;
}

.alert-urgent {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

.alert-muted {
  background-color: #e2e3e5;
  color: #383d41;
  border: 1px solid #d6d8db;
}

.alert-glow {
  background-color: #d1ecf1;
  color: #0c5460;
  border: 1px solid #bee5eb;
  box-shadow: 0 0 15px rgba(0, 123, 255, 0.5);
}

.Diet button {
  color: black;
}

.Diet button:hover {
  color: black;
  background-color: rgb(238, 238, 238);
}

.Diet .active {
  background-color: #414141 !important;
  color: white !important;
  font-weight: 500;
}

.log {
  border: none;
  background-color: rgb(0, 89, 255);
  padding: 5px 10px;
  border-radius: 0.3rem;
  color: white;
  transition: 0.2s;
}

.log:hover {
  background-color: rgb(3, 50, 136);
}

.card {
  width: 100%;
  position: absolute;
  display: flex;
  top: 0%;
  right: 0%;
  z-index: 1;
  min-height: 100vh;
  border-radius: 0%;
}

.sub {
  display: flex;
  justify-content: center;
}

.gen {
  background-color: rgb(255, 255, 255);
  border: 1px solid black;
  border-radius: 0.3rem;
  padding: 5px 10px;
}

.gen:hover {
  background-color: rgb(180, 215, 247);
}

@media (max-width: 600px) {
  .data-img{
    min-width: 30% !important;
  }
}


.title{
  display: flex !important;
    justify-content: space-between;
    align-items: center !important;
}

.title p{
  margin: 0%;
}